<template>
	<view>
		<!-- 地址 -->
		<navigator url="/pages/address/address?source=1" class="address-section">
			<view class="order-content">
				<text class="yticon icon-shouhuodizhi"></text>
				<view class="cen" >
					<view class="top">
						<text class="name">小丑竟是</text>
						<text class="mobile">13112345678</text>
					</view>
					<text class="address">北京 北京 东城区</text>
				</view>
				
<!-- 				<view class="cen" >
					<view class="top"><text class="name">请设置收获地址</text></view>
				</view> -->
				<text class="yticon icon-you"></text>
			</view>

			<image
				class="a-bg"
				src="data:image/png;base64,"
			></image>
		</navigator>

		<view class="goods-section">
			<!-- 	<view class="g-header b-b">
				<image class="logo" src="http://duoduo.qibukj.cn/./Upload/Images/20190321/201903211727515.png"></image>
				<text class="name">西城小店铺</text>
			</view> -->
			<!-- 商品列表 -->
			<view class="g-item" >
				<image :src="orderArr.img"></image>
				<view class="right">
					<text class="title">{{orderArr.name}}</text>
					<text class="spec">{{orderArr.attr}}</text>
					<view class="price-box">
						<text class="price">￥{{orderArr.price}}</text>
						<text class="number">x {{orderArr.num}}</text>
					</view>
				</view>
			</view>
		</view>

		<!-- 金额明细 -->
		<view class="yt-list">
			<view class="yt-list-cell b-b">
				<text class="cell-tit ">商品金额</text>
				<text class="cell-tip">￥{{orderArr.price * orderArr.num}}</text>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tit ">优惠金额</text>
				<text class="cell-tip" style="color: #FDD100;">无优惠</text>
			</view>
			<view class="yt-list-cell b-b">
				<text class="cell-tit ">运费</text>
				<text class="cell-tip">免运费</text>
			</view>
			<view class="yt-list-cell desc-cell">
				<text class="cell-tit ">备注</text>
				<input class="desc" type="text" v-model="beizhu"  placeholder="请填写备注信息" placeholder-class="placeholder" />
			</view>
		</view>

		<!-- 底部 -->
		<view class="footer">
			<view class="price-content">
				<text>总计</text>
				<text class="price-tip">￥</text>
				<text class="price">{{orderArr.price * orderArr.num}}</text>
			</view>
			<text class="submit" >提交订单</text>
		</view>

	
	</view>
</template>

<script>
export default{
	data(){
		return{
			cart_id:0,//购物车id
			uid:0,//uid
			token:0,//token
			orderArr:[],//确认订单数组
			beizhu:'',//备注
		}
	},
	onLoad(e) {
		this.cart_id = e.cart_id
		this.getUid_token()
		this.checkOrder()
	},
	methods:{
		//获取uid和token
		getUid_token(){
			this.uid = uni.getStorageSync('uid')
			this.token = uni.getStorageSync('token')
		},
		checkOrder(){
			this.$http.post('/order/checkOrder',
			{uid:this.uid,cart_id:this.cart_id,token:this.token,type:'buy'}
			)
			.then((res)=>{
				console.log(res);
				this.orderArr = res.data.goods[0]
			})
			.catch((err)=>{
				console.log(err);
			})
		}
	}
}
</script>

<style lang="scss">
page {
	padding-bottom: 100upx;
}

.address-section {
	padding: 30upx 0;
	background: #fff;
	position: relative;

	.order-content {
		display: flex;
		align-items: center;
	}

	.icon-shouhuodizhi {
		flex-shrink: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 90upx;
		color: #888;
		font-size: 44upx;
	}

	.cen {
		display: flex;
		flex-direction: column;
		flex: 1;
		font-size: 28upx;
	}

	.name {
		font-size: 34upx;
		margin-right: 24upx;
	}

	.address {
		margin-top: 16upx;
		margin-right: 20upx;
	}

	.icon-you {
		font-size: 32upx;
		margin-right: 30upx;
	}

	.a-bg {
		position: absolute;
		left: 0;
		bottom: 0;
		display: block;
		width: 100%;
		height: 5upx;
	}
}

.goods-section {
	// margin-top: 16upx;
	background: #fff;
	padding-bottom: 1px;
	padding-top: 24upx;

	.g-header {
		display: flex;
		align-items: center;
		height: 84upx;
		padding: 0 30upx;
		position: relative;
	}

	.logo {
		display: block;
		width: 50upx;
		height: 50upx;
		border-radius: 100px;
	}

	.name {
		font-size: 30upx;
		margin-left: 24upx;
	}

	.g-item {
		display: flex;
		margin: 20upx 30upx;
		border-bottom: 1px solid #eee;
		padding-bottom: 16upx;
        
		image {
			flex-shrink: 0;
			display: block;
			width: 140upx;
			height: 140upx;
			border-radius: 4upx;
		}

		.right {
			flex: 1;
			padding-left: 24upx;
			overflow: hidden;
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
		}

		.title {
			font-size: 30upx;
		}

		.spec {
			font-size: 26upx;
			width: 100%;
		}

		.price-box {
			display: flex;
			width: 100%;
			align-items: center;
			font-size: 32upx;
			padding-top: 10upx;
			align-self: flex-end;
			justify-content: space-between;

			.price {
				margin-bottom: 4upx;
				color:#FDD100 ;
			}
			.number {
				font-size: 26upx;
				margin-left: 20upx;
			}
		}

		.step-box {
			position: relative;
		}
	}
}
.yt-list {
	margin-top: 16upx;
	background: #fff;
}

.yt-list-cell {
	display: flex;
	align-items: center;
	padding: 10upx 30upx 10upx 40upx;
	line-height: 70upx;
	position: relative;

	&.cell-hover {
		background: #fafafa;
	}

	&.b-b:after {
		left: 30upx;
	}

	.cell-icon {
		height: 32upx;
		width: 32upx;
		font-size: 22upx;
		color: #fff;
		text-align: center;
		line-height: 32upx;
		background: #f85e52;
		border-radius: 4upx;
		margin-right: 12upx;

		&.hb {
			background: #ffaa0e;
		}

		&.lpk {
			background: #3ab54a;
		}
	}

	.cell-more {
		align-self: center;
		font-size: 24upx;
		margin-left: 8upx;
		margin-right: -10upx;
	}

	.cell-tit {
		flex: 1;
		font-size: 26upx;
		margin-right: 10upx;
	}

	.cell-tip {
		font-size: 26upx;
	}

	&.desc-cell {
		.cell-tit {
			max-width: 90upx;
		}
	}

	.desc {
		flex: 1;
	}
}

/* 支付列表 */
.pay-list {
	padding-left: 40upx;
	margin-top: 16upx;
	background: #fff;
	.pay-item {
		display: flex;
		align-items: center;
		padding-right: 20upx;
		line-height: 1;
		height: 110upx;
		position: relative;
	}
	.icon-weixinzhifu {
		width: 80upx;
		font-size: 40upx;
		color: #6bcc03;
	}
	.icon-alipay {
		width: 80upx;
		font-size: 40upx;
		color: #06b4fd;
	}
	.icon-xuanzhong2 {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60upx;
		height: 60upx;
		font-size: 40upx;
	}
	.tit {
		font-size: 32upx;
		flex: 1;
	}
}

.footer {
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 995;
	display: flex;
	align-items: center;
	width: 100%;
	height: 90upx;
	justify-content: space-between;
	font-size: 30upx;
	background-color: #fff;
	z-index: 998;
	box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1);
	.price-content {
		padding-left: 30upx;
	}
	.price-tip {
		margin-left: 8upx;
		color: #FDD100;
	}
	.price {
		font-size: 36upx;
		color: #FDD100;
	}
	.submit {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 280upx;
		height: 100%;
		color: #fff;
		font-size: 32upx;
		background-color: rgb(253, 209, 0);
	}
}

/* 优惠券面板 */
.mask {
	display: flex;
	align-items: flex-end;
	position: fixed;
	left: 0;
	top: var(--window-top);
	bottom: 0;
	width: 100%;
	background: rgba(0, 0, 0, 0);
	z-index: 9995;
	transition: 0.3s;

	.mask-content {
		width: 100%;
		min-height: 30vh;
		max-height: 70vh;
		background: #f3f3f3;
		transform: translateY(100%);
		transition: 0.3s;
		overflow-y: scroll;
	}
	&.none {
		display: none;
	}
	&.show {
		background: rgba(0, 0, 0, 0.4);

		.mask-content {
			transform: translateY(0);
		}
	}
}

/* 优惠券列表 */
.coupon-item {
	display: flex;
	flex-direction: column;
	margin: 20upx 24upx;
	background: #fff;
	.con {
		display: flex;
		align-items: center;
		position: relative;
		height: 120upx;
		padding: 0 30upx;
		&:after {
			position: absolute;
			left: 0;
			bottom: 0;
			content: '';
			width: 100%;
			height: 0;
			border-bottom: 1px dashed #f3f3f3;
			transform: scaleY(50%);
		}
	}
	.left {
		display: flex;
		flex-direction: column;
		justify-content: center;
		flex: 1;
		overflow: hidden;
		height: 100upx;
	}
	.title {
		font-size: 32upx;
		margin-bottom: 10upx;
	}
	.time {
		font-size: 24upx;
	}
	.right {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: 26upx;
		height: 100upx;
	}
	.price {
		font-size: 44upx;
		&:before {
			content: '￥';
			font-size: 34upx;
		}
	}
	.tips {
		font-size: 24upx;
		line-height: 60upx;
		padding-left: 30upx;
	}
	.circle {
		position: absolute;
		left: -6upx;
		bottom: -10upx;
		z-index: 10;
		width: 20upx;
		height: 20upx;
		background: #f3f3f3;
		border-radius: 100px;
		&.r {
			left: auto;
			right: -6upx;
		}
	}
}
</style>
